.profile {
  display: flex;
  flex-direction: column;
  gap: 50px;
  max-width: 400px;
  width: 90%;
  height: auto;
  padding: 24px 0 0;
  background-color: var(--white-color);
  border: 1px solid var(--royal-blue-color);
  border-radius: 4px;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.description {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}

.profile .avatar {
  display: block;
  width: 60%;
  background-color: var(--components-background-color);
  border-radius: 50%;
}

.name,
.tag,
.location {
  font-size: 20px;
  color: var(--grey-color);
}

.name {
  font-size: 28px;
  font-weight: bold;
  color: var(--black-color);
}

.stats {
  display: flex;
  border-top: var(--royal-blue-color) 1px solid;
  background-color: var(--components-background-color);
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.stats > li {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  padding: 20px 0;
  width: calc(100% / 3);
}

.stats > li:not(:last-of-type) {
  border-right: var(--royal-blue-color) 1px solid;
}

.label {
  font-size: 18px;
  color: var(--grey-color);
}

.quantity {
  font-size: 22px;
  font-weight: bold;
  color: var(--black-color);
}
